<template>
  <div>
    <ul class="flex">
      <template v-for="(item, index) in tabList()" :key="index">
        <li
            class="cursor-pointer py-2 px-4 text-gray-500 border-b-8"
            :class="activeTab === item.key ? 'text-' + defaultColor + '-500 border-' + defaultColor + '-500' : ''"
            @click="activeTab = item.key"
            v-if="false">{{item.title}}</li>
        <li class="cursor-pointer py-3 px-4 rounded-t transition"
  					:class="activeTab === item.key ? 'bg-' + defaultColor + '-500 text-white' : ' text-gray-500'"
            @click="activeTab = item.key"
            v-if="true">{{item.title}}</li>
      </template>
    </ul>
    <div
        class="relative w-full bg-white text-center mx-auto overflow-auto border border-t-4"
        :class="'border-' + defaultColor + '-500'"
        style="height: calc(100vh - 280px);">
      <adminCrudWithLogin v-show="activeTab === 'adminCrudWithLogin'" />
      <test v-show="activeTab === 'test'" />
    </div>
  </div>
</template>
<script>
import config from '@/config'
import adminCrudWithLogin from './demo/adminCrudWithLogin/index.vue'
import test from './demo/test.vue'
export default {
  name: 'demo',
  components: {
    adminCrudWithLogin,
    test
  },
  data () {
    return {
      config,
      activeTab: ''
    }
  },
  computed: {
    defaultColor () {
      return this.config.colorName
    }
  },
  methods: {
    tabList () {
      let result = [
        { key: 'adminCrudWithLogin', title: this.$t('demo.adminCrudWithLogin') },
        { key: 'test', title: 'test' }
      ]
      if (this.activeTab === '') {
        this.activeTab = result[0].key
      }
      return result
    }
  }
}
</script>
