<template>
  <div :class="'border-2 border-' + defaultColor + '-600 p-10 pt-5 shadow-sm mx-auto flex rounded w-10/12 md:w-1/3 text-left'">
    <div class="sec flex-1">
      <div class="mb-5 text-center">
        <mylogo class="inline-block"></mylogo>
      </div>
      <span class="flex shadow-md mb-5 text-xs">
        <span :class="'bg-' + defaultColor + '-500 w-28 font-bold text-center text-gray-200 p-3 px-5 rounded-l'">Email</span>
        <input v-model="loginform.email" class="field text-sm text-gray-600 p-2 px-3 rounded-r w-full" type="text" placeholder="someonespecial@example.com">
      </span>
      <span class="flex shadow-md mb-5 text-xs">
        <span :class="'bg-' + defaultColor + '-500 w-28 font-bold text-center text-gray-200 p-3 px-5 rounded-l'">Password</span>
        <input v-model="loginform.password" class="field text-sm text-gray-600 p-2 px-3 rounded-r w-full" type="password" placeholder="">
      </span>
      <a :class="'text-' + defaultColor + '-500 hover:underline font-bold text-xs ml-auto cursor-pointer'">Forget Password ?</a>
      <span @click="login" :class="'border-2 border-' + defaultColor + '-500 bg-' + defaultColor + '-500 hover:bg-gray-100 text-gray-100 hover:text-' + defaultColor + '-500 mt-3 block text-center p-3 px-4 text-sm rounded cursor-pointer font-bold'">Login</span>
    </div>
  </div>
</template>
<script>
import mylogo from '@/components/logo.vue'
export default {
  props: {
    defaultColor: {
      default: 'green'
    },
    loginFunction: Function
  },
  components: {
    mylogo
  },
  data () {
    return {
      loginform: {
        email: 'abc@test.com',
        password: '123456789'
      }
    }
  },
  methods: {
    login () {
      if (this.loginFunction) {
        this.loginFunction(this.loginform)
      }
    }
  }
}
</script>
