<template>
  <div>
    <div class="flex space-2 items-center border-b-2 pb-4">
      <mylogo class="inline-block transform -rotate-90 md:rotate-0 my-4 md:m-auto"></mylogo>
    </div>
    <div class="mt-8">
      <ul class="space-y-4">
        <li v-for="(menuItem, menuIndex) in dashboardmenu" :key="menuIndex">
          <a
              href="#"
              @click="setMenu(menuItem.key)"
              :title="menuItem.title"
              :class="menuTab === menuItem.key ? 'text-white bg-indigo-600' : 'text-gray-500 hover:text-indigo-600'"
              class="flex items-center text-sm font-semibold rounded-md p-2">
            <svgicon
                :icon="menuItem.icon"
                :dclass="'m-auto md:mr-4 md:ml-0 transition duration-200 ' + (menuTab === menuItem.key ? 'h-8 w-8' : 'h-6 w-6')"></svgicon>
            <span class="hidden md:inline">{{menuItem.title}}</span>
          </a>
        </li>
        <li>
          <a href="#" @click="logoutFunction" title="Logout" class="flex items-center text-sm font-semibold p-2 text-red-400 hover:text-red-600">
            <svgicon icon="logout" dclass="h-6 w-6 m-auto mr-4 md:ml-0"></svgicon>
            <span class="hidden md:inline">Logout</span>
          </a>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import mylogo from '@/components/logo.vue'
import svgicon from '@/components/svg.vue'
export default {
  props: {
    defaultColor: {
      default: 'green'
    },
    dashboardmenu: Array,
    setMenuFunction: Function,
    logoutFunction: Function,
    menuTab: String
  },
  components: {
    mylogo,
    svgicon
  },
  methods: {
    setMenu (p) {
      if (this.setMenuFunction) {
        this.setMenuFunction(p)
      }
    }
  }
}
</script>
