<template>
  <div class="w-full relative">
    <div v-if="!auth" class="mt-10">
      <loginSession
          :defaultColor="defaultColor"
          :loginFunction="login"></loginSession>
    </div>
    <div v-if="auth">
      <div class="min-h-full flex">
        <div class="py-12 px-2 md:px-10 w-2/12 2xl:w-1/4">
          <sidebar
              :defaultColor="defaultColor"
              :dashboardmenu="dashboardmenu"
              :setMenuFunction="setMenu"
              :logoutFunction="logout"
              :menuTab="menuTab"></sidebar>
        </div>
        <div class="bg-indigo-50 py-12 px-10 w-10/12 2xl:w-3/4">
          <div>
            <div v-show="menuTab === 'dashboard'">
              <dashboardSession
                  :defaultColor="defaultColor"></dashboardSession>
            </div>
            <div v-if="menuTab !== 'dashboard' && dummyTableData[menuTab]">
              <crudSession
                  :defaultColor="defaultColor"
                  :dummyTableData="dummyTableData"
                  :menuTab="menuTab"></crudSession>
            </div>
            <div></div>
          </div>
          <div></div>
          <div></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script scoped>
// import config from '@/config'
import sidebar from './sidebar.vue'
import loginSession from './pages/login.vue'
import dashboardSession from './pages/dashboard.vue'
import crudSession from './pages/crud.vue'
export default {
  name: 'adminCrudWithLogin',
  components: {
    sidebar,
    loginSession,
    dashboardSession,
    crudSession
  },
  data () {
    return {
      // config,
      auth: false,
      menuTab: ''
    }
  },
  mounted () {
    if (this.menuTab === '') {
      this.setFirstPage()
    }
  },
  computed: {
    defaultColor () {
      // return this.config.colorName
      return 'indigo'
    },
    dashboardmenu () {
      return [
        { title: 'Dashboard', icon: 'dashboard', key: 'dashboard' },
        { title: 'Customer', icon: 'users', key: 'customer' },
        { title: 'Product', icon: 'products', key: 'product' }
      ]
    },
    dummyTableData () {
      return {
        customer: {
          title: 'Customer',
          total: 5,
          tableColumn: ['name', 'email'],
          data: [
            { name: 'John', email: 'john@gmail.com' },
            { name: 'Chris', email: 'chris@gmail.com' },
            { name: 'Mabel', email: 'mabel@gmail.com' },
            { name: 'Lily', email: 'lily@gmail.com' },
            { name: 'Wesly', email: 'wesly@gmail.com' }
          ]
        },
        product: {
          title: 'Product',
          tableColumn: ['title', 'stock'],
          total: 2,
          data: [
            { title: 'Table 24x24', stock: 50 },
            { title: 'Chair', stock: 3 },
            { title: 'Chair', stock: 3 },
            { title: 'Chair', stock: 3 },
            { title: 'Chair', stock: 3 },
            { title: 'Chair', stock: 3 },
          ]
        }
      }
    }
  },
  methods: {
    setFirstPage () {
      this.menuTab = 'dashboard'
    },
    login (p) {
      if (p) {
        this.auth = true
        this.setFirstPage()
      }
    },
    setMenu (p) {
      this.menuTab = p
    },
    logout () {
      this.auth = false
    }
  }
}
</script>
